
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'tagInfo',
data() {
return {
lists: []
}
},
computed: {

},
components: {
XHeader,
Flexbox,
FlexboxItem
},
mounted() {
this.getList()
},
methods: {
getList() {
let _this = this;
this.post({
url: "/fundMsg/querySectorFund/v1.0",
data: {
sectorFundType: _this.$route.query.sectorFundType
},
success: function(e) {
if (e.code == "0000") {
_this.lists = e.result.list;
}
}
})
}
}
}



